import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import styles from "./styles.module.css";
import logo from "../assets/logo.png";
import {
  Alert,
  Center,
  CircularProgress,
  AlertIcon,
  useToast,
  Button,
} from "@chakra-ui/react";
import { Link } from "react-router-dom";
import { addStaff } from "../redux/actions/userActions";
import { USER_REGISTER_RESET } from "../redux/constants/userConstants";

const Register = ({ history }) => {
  const dispatch = useDispatch();
  const toast = useToast();

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [msg, setMsg] = useState(false);

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const userRegister = useSelector((state) => state.userRegister);
  const { loading, error, success } = userRegister;

  const submitHandler = (e) => {
    e.preventDefault();
    if (password !== confirmPassword) {
      setMsg(true);
    } else {
      setMsg(false);
      dispatch(addStaff(email, password));
    }
  };
  useEffect(() => {
    if (userInfo) {
      history.push("/dashboard");
    }
  }, [history, userInfo]);
  if (error) {
    toast({
      title: "Error",
      description: error,
      status: "error",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: USER_REGISTER_RESET });
  }
  if (success) {
    toast({
      title: "Account Created",
      description: "Registration Successful",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: USER_REGISTER_RESET });
    history.push("/");
  }
  return (
    <div className={styles.app}>
      <div className={styles.login}>
        <div className={styles.loginCard}>
          <div className={styles.logo}>
            <Center>
              <img src={logo} alt="Lotus Beta Analytics" />
            </Center>
          </div>
          <h4 className="text-center">Lotus Beta Analytics Nigeria</h4>
          <h6 className="text-center">Employee's Portal</h6>
          <div className={`${styles.forms}`}>
            {error && (
              <Alert status="error">
                <AlertIcon />
                {error}
              </Alert>
            )}
            {msg && (
              <Alert status="warning">
                <AlertIcon />
                <span style={{ color: "black" }}>Password does not match</span>
              </Alert>
            )}
            {loading ? (
              <CircularProgress isIndeterminate color="purple.300" />
            ) : (
              <>
                <form onSubmit={submitHandler}>
                  <div className={`col-md-12 ${styles.formPadding}`}>
                    <input
                      type="email"
                      className="form-control"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      placeholder="Enter Email Address"
                    />
                  </div>
                  <div className={`col-md-12 ${styles.formPadding}`}>
                    <input
                      type="password"
                      className="form-control"
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                      placeholder="Enter Password"
                    />
                  </div>
                  <div className={`col-md-12 ${styles.formPadding}`}>
                    <input
                      type="password"
                      className="form-control"
                      value={confirmPassword}
                      onChange={(e) => setConfirmPassword(e.target.value)}
                      placeholder="Confirm Password"
                    />
                  </div>
                  <div className={`col-md-12 ${styles.formPadding}`}>
                    <input
                      type="submit"
                      className={`form-control btn ${styles.warning}`}
                      value="REGISTER"
                    />
                  </div>
                  <div style={{ textAlign: "center" }}>
                    <span>
                      &nbsp;Already Have an Account?{" "}
                      <Link to="/">
                        <Button colorScheme="orange" size="sm">
                          Login
                        </Button>
                      </Link>
                    </span>
                  </div>
                  <div
                    className={`col-md-12 ${styles.formPadding} text-center`}
                  >
                    <small>
                      &copy; {new Date().getFullYear()} All Rights and Reserved
                      <br />
                      Lotus Beta Analytics Nigeria Limited
                    </small>
                  </div>
                </form>
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Register;
