import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import styles from "./styles.module.css";
import logo from "../assets/logo.png";
import {
  Alert,
  Center,
  CircularProgress,
  AlertIcon,
  useToast,
  Button,
} from "@chakra-ui/react";
import { Link } from "react-router-dom";
import { addStaff } from "../redux/actions/userActions";
import { USER_REGISTER_RESET } from "../redux/constants/userConstants";

const VerifyEmail = ({ history, match }) => {
  const dispatch = useDispatch();
  const id = match.params.id;
  const code = match.params.code;

  const userLogin = useSelector((state) => state.userLogin);
  const { userInfo } = userLogin;

  const userRegister = useSelector((state) => state.userRegister);
  const { loading, error, success } = userRegister;

  dispatch(addStaff(id, code));

  useEffect(() => {
    dispatch(addStaff(id, code));
  }, [dispatch, id, code]);
  if (error) {
    dispatch({ type: USER_REGISTER_RESET });
  }
  return (
    <div className={styles.app}>
      <div className={styles.login}>
        <div className={styles.loginCard}>
          <div className={styles.logo}>
            <Center>
              <img src={logo} alt="Lotus Beta Analytics" />
            </Center>
          </div>
          <h4 className="text-center">Lotus Beta Analytics Nigeria</h4>
          <h6 className="text-center">Employee's Portal</h6>
          <div className={`${styles.forms}`}>
            {error && (
              <Alert status="error">
                <AlertIcon />
                {error}
              </Alert>
            )}

            {loading && (
              <>
                <CircularProgress isIndeterminate color="purple.300" />
                Email Verification in Progress.....
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default VerifyEmail;
